import React from 'react';
import Data from '../../data/db.json';
import { ListGroup, Row, Col } from 'react-bootstrap';
import { Link } from 'react-router-dom';

function OrganizationWidget() {
    let professionalExp = Data.professionalExperiences;

    function organizationGrid() {
        return (
            <Row className="organization-widget-pad">
                {
                    professionalExp.map((item, index) => {
                        return (
                            <Col md={2} key={index}>
                                <Link to={"/organizations/"+item.organizationName}>
                                    <img src={require('../../assets/' + item.organizationLogoFile)}
                                        alt={item.organizationName}
                                        width={40} />
                                </Link>
                            </Col>

                        );
                    })
                }
            </Row>
        );
    }
    return (
        <>
            <hr />
            <h6>
                Organizations
            </h6>
            {organizationGrid()}
        </>
    );
}

export default OrganizationWidget;