import React from 'react';
import { Navbar, Nav, Form, FormControl, Button } from 'react-bootstrap';
// @ts-ignore
import * as ProfileLogoLight from '../../assets/profile-logo-light.png';
import Data from '../../data/db.json'

function TopNavigationBar() {
    return (
        <Navbar variant="dark" className={"nav-color justify-content-center"}>
            <Navbar.Brand>
                <img
                    src={ProfileLogoLight}
                    width="40"
                    height="40"
                    className="d-inline-block align-top"
                    alt={Data.commonDetails.name + " | " + Data.commonDetails.role}
                />
            </Navbar.Brand>
        </Navbar>
    );
}

export default TopNavigationBar;