import React, { useState } from 'react';
import { useParams } from 'react-router-dom';
import AppMarkdown from '../basic/app-markdown';
import Data from '../../data/db.json';

function OrganizationProfile() {
    let { name } = useParams();
    let organization = {};

    if (name != "") {
        Data.professionalExperiences.map(item => {
            if (item.organizationName === name) {
                organization = item;
            }
        })
    }
    return (
        <>
            Worked with<h2> {name}</h2>
            <hr />
            <AppMarkdown
                fileName={organization['workDescFile']}
                moduleType={'organization'}></AppMarkdown>
        </>
    );
}

export default OrganizationProfile;