import React, { useState, useEffect } from 'react';
import ReactMarkdown from 'react-markdown';
// import ContentUrl from '../../content/organizations/cbre-project.md';

function AppMarkdown(props) {
    let { moduleType, fileName } = props;
    const [mdText, setMdText] = useState('');
    var fileUrl = "";
    try {
        fileUrl = require('../../content/' + moduleType + '/' + fileName);
    }
    catch{
    }
    
    useEffect(() => {
        if (fileUrl != "") {
            fetch(fileUrl)
            .then((response) => {
                if (response.ok) return response.text();
                else return Promise.reject("Didn't fetch text correctly");
            })
            .then((text) => {
                setMdText(text);
            })
            .catch((error) => console.error(error));
        }
    });

    return (
        <ReactMarkdown source={mdText}></ReactMarkdown>
    );
}

export default AppMarkdown;